import React from 'react'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'

import { NavBar } from '../components'
import { MembersList, MemberInsert, MemberUpdate } from '../pages'

import 'bootstrap/dist/css/bootstrap.min.css'


function App() {
  return (
      <Router>
          <NavBar />
          <Switch>
                <Route path="/movies/list" exact component={MembersList} />
                <Route path="/movies/create" exact component={MemberInsert} />
                <Route
                    path="/movies/update/:id"
                    exact
                    component={MemberUpdate}
                />
            </Switch>
      </Router>
  )
}

export default App
